.instructor-template-default{
	.qode-instructor-single-holder{
		.qode-instructor-single-outer{
			.qode-instructor-single-info-holder{
				.qode-ts-info-holder{
					.qode-instructor-image{
						margin-bottom: 20px;

						img{
							border-radius: 5px;
						}
					}

					.qode-title{
						text-transform: uppercase;
					}

					.qode-social{
						margin-top: 30px;
						.qode_icon_shortcode {
							height: 2em;
						    width: 2em;
						    text-align: center;
						    line-height: 2em;
						    font-size: 15px;
						    border-radius: 50%;
						    background-color: white;
						    margin-right: 8px;
						    border: 1px solid white;
						}
					}
				}

				.qode-ts-content-holder{
					.qode-tabs{
						.qode-tabs-nav{
							list-style-type: none;
						    background-color: #e4ebfb;
						    border-radius: 5px 5px 0 0;
						    display: inline-block;

						    li{
								float: left;

								&.ui-state-active{
									a{
										background-color: white;
    									border-radius: 5px 5px 0 0;
									}
								}

								a{
									padding: 22px 41px;
									float: left;
								}
							}
						}
						.qode-tab-container{
							background-color: #FFF;
							margin-top: -8px;
							border-radius: 0 5px 5px 5px;
    						-moz-box-shadow: 4px 4px 4px #e5ecfb;
    						-webkit-box-shadow: 4px 4px 4px #e5ecfb;
    						box-shadow: 8px 8px 9px #e5ecfb;
						}
					}
				}
			}
		}
	}
}